import barTitle from '@/components/zaiui-common/basics/bar-title';
import _tool from '@/utils/tools.js';	//工具函数
export default {
	components: {
		barTitle
	},
	data() {
		return {
			status: false,
		}
	},
	onLoad() {
		
	},
	onReady() {
		_tool.setBarColor(true);
		uni.pageScrollTo({
		    scrollTop: 0,
		    duration: 0
		});
	},
	methods: {
		statusTap() {
			if(this.status) {
				this.status = false;
			} else {
				this.status = true;
			}
		},
		btnTap() {
			uni.navigateTo({
				url: "/pages/real_name/form"
			});
		}
	}
}
